'use client';

import { StarIcon } from '@heroicons/react/20/solid';

// Define a more specific type for the review object for better type safety
interface Review {
  id: string;
  comment: string | null;
  rating: number;
  user: {
    name?: string | null;
  };
  product?: {
    name: string;
  } | null;
  employee?: {
    name: string;
  } | null;
}

export default function ReviewCard({ review }: { review: Review }) {
    const title = review.product?.name || review.employee?.name || 'General Feedback';
    const type = review.product ? 'Product' : 'Employee';
  
    return (
      <div className="bg-white/80 backdrop-blur-sm rounded-xl shadow-lg p-6 border border-white/20 h-full">
        <div className="flex items-start justify-between mb-4">
          <div>
            <p className="font-bold text-lg text-gray-800">{title}</p>
            <p className="text-sm text-gray-500">by {review.user.name || 'Anonymous'}</p>
          </div>
          <span className={`px-2 py-1 text-xs font-semibold rounded-full ${
            type === 'Product' ? 'bg-purple-100 text-purple-800' : 'bg-orange-100 text-orange-800'
          }`}>
            {type} Review
          </span>
        </div>
        {review.comment && <p className="text-gray-700 italic mb-4">"{review.comment}"</p>}
        <div className="flex items-center">
          {[...Array(5)].map((_, i) => (
            <StarIcon
              key={i}
              className={`h-5 w-5 ${i < review.rating ? 'text-yellow-400' : 'text-gray-300'}`}
            />
          ))}
        </div>
      </div>
    );
} 